import React from 'react';
import { NavLink } from "react-router-dom";

const Common = (props) => {
    return (
        <>
            <section id="header" className="d-flex align-items-center">
                <div className="container">
                    <div className="row">
                        <div className="col-10 mx-auto">
                            <div className="row">
                                <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex flex-column justify-content-center">
                                    <h1>{props.text}<strong className="brand-name"><br/>Brand Name</strong></h1>
                                    <h2 className="my-3">
                                        Lorem ipsum dolor sit amet.
                                    </h2>
                                    <div className="mt-3">
                                        <NavLink to={props.visit} className="btn-get-started">{props.btnText}</NavLink>
                                    </div>
                                </div>

                                <div className="col-lg-6 order-1 oder-lg-2 header-img">
                                    <img src={props.imgsrc} className="img-fluid animated" alt="Bussiness man" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}

export default Common;